/* table.css */

/* Apply some styles to the table */
table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
  font-size: 16px;
}

/* Style the table header */
table thead {
  background-color: #f2f2f2;
}

table th,
table td {
  padding: 8px;
  border-bottom: 1px solid #ddd;
  text-align: left;
}

/* Style the table rows */
table tbody tr:nth-child(even) {
  background-color: #f2f2f2;
}

/* Style the delete button */
button {
  padding: 6px 12px;
  background-color: #ff0000;
  color: #fff;
  border: none;
  cursor: pointer;
  font-size: 14px;
  border-radius: 4px;
}

/* Style the form */
form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-width: 400px;
}

form label {
  font-weight: bold;
}

form input[type="text"] {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

form button[type="submit"] {
  background-color: #008000;
}

/* Media query for mobile devices */
@media only screen and (max-width: 768px) {
  /* Reduce font size for better mobile display */
  table,
  table th,
  table td {
    font-size: 14px;
  }

  /* Center align table cells for mobile */
  table th,
  table td {
    text-align: center;
  }

  /* Adjust form width and padding for mobile */
  form {
    max-width: 100%;
    padding: 0 10px;
  }

  /* Increase button font size for mobile */
  button {
    font-size: 16px;
  }
}

/* Additional Styles for Desktop */
/* Center align table cells for desktop */
table th,
table td {
  text-align: center;
}

/* Adjust form width for desktop */
form {
  max-width: 600px;
  margin: 0 auto;
}

/* Adjust button alignment for desktop */
form button[type="submit"] {
  align-self: flex-start;
}
